import "./styles.css";
var but = document.getElementById("ball");
ball.style.top = Math.floor(Math.random() * 50).toString() + "px";
ball.style.left = Math.floor(Math.random() * 50).toString() + "px";

window.addEventListener("keydown", function (event) {
  let code = event.keyCode;

  if (code == 87) {
    if (ball.style.top == "0px") {
      return;
    }

    let curr = ball.style.top;
    let lcurr = ball.style.top.length;
    let nudistance = parseInt(curr.substring(0, lcurr - 2));
    nudistance--;
    ball.style.top = nudistance.toString() + "px";
  } else if (code == 65) {
    if (ball.style.left == "0px") {
      return;
    }

    let curr = ball.style.left;
    let lcurr = ball.style.left.length;
    let nudistance = parseInt(curr.substring(0, lcurr - 2));
    nudistance--;
    ball.style.left = nudistance.toString() + "px";
  } else if (code == 83) {
    if (ball.style.top == (window.innerHeight - 200).toString() + "px") {
      return;
    }

    let curr = ball.style.top;
    let lcurr = ball.style.top.length;
    let nudistance = parseInt(curr.substring(0, lcurr - 2));
    nudistance++;
    ball.style.top = nudistance.toString() + "px";
  } else if (code == 68) {
    if (ball.style.left == (window.innerWidth - 200).toString() + "px") {
      return;
    }

    let curr = ball.style.left;
    let lcurr = ball.style.left.length;
    let nudistance = parseInt(curr.substring(0, lcurr - 2));
    nudistance++;
    ball.style.left = nudistance.toString() + "px";
  }
});
